import React from 'react';
import axios from 'axios';
import { ENV as DefaultEnv } from '../env';
import LFGActivity from '../core/lfg_activity';
import Environment from '../env_interface';
import moment from 'moment-timezone';

// polyfill ENV
const ENV = ((window as unknown as { [key: string]: unknown })['ENV'] as Environment) || DefaultEnv;

export interface FeedProperties {
    name: string;
    interval: number;
}

export interface FeedState {
    setup: boolean;
    lfgs: LFGActivity[];
}

export class Feed extends React.Component<FeedProperties, FeedState> {
    private colorMap = {
        0: {
            normal: 'bg-amber-400 text-black',
            hover: 'hover:bg-amber-300 hover:text-black',
        },
        1: {
            normal: 'bg-teal-400 text-black',
            hover: 'hover:bg-teal-600 hover:text-black',
        },
        2: {
            normal: 'bg-orange-600 text-white',
            hover: 'hover:bg-orange-400 hover:text-black',
        },
    };

    public constructor(props: FeedProperties) {
        super(props);

        this.state = {
            setup: true,
            lfgs: [],
        };

        this.requestFeed = this.requestFeed.bind(this);
    }

    public requestFeed() {
        axios
            .post(ENV.hosts.api + '/feed/get', {
                application: ENV.api.application,
                name: this.props.name,
            })
            .then((response) => {
                if (
                    response.data &&
                    typeof response.data['success'] !== undefined &&
                    response.data['success'] === true &&
                    typeof response.data['response']['feed'] !== 'undefined' &&
                    typeof response.data['response']['feed']['data'] !== 'undefined'
                ) {
                    // todo
                    const activityFeedJSON = response.data['response']['feed']['data'];
                    let activityFeed: LFGActivity[] | undefined = undefined;
                    try {
                        activityFeed = JSON.parse(activityFeedJSON);
                    } catch {
                        console.log('Unable to parse JSON feed');
                    }

                    if (activityFeed !== undefined) {
                        this.setState({
                            setup: false,
                            lfgs: activityFeed,
                        });
                    }
                }
            })
            .catch(() => {
                console.log('Failed to get feed');
            });
    }

    public componentDidMount() {
        this.requestFeed();
    }

    public renderSetup() {
        console.log('Loading in feed');
        return <div>Setup Here</div>;
    }

    public renderFeed() {
        console.log(this.state.lfgs);
        console.log('Timezone guess: ', moment.tz.guess());
        const targetTimezone = moment.tz.guess();
        for (let i = 0; i < 24; i++) {
            console.log('Index: ', i, 'Modulo', i % 3);
        }
        return (
            <div className="feed p-4" data-name={this.props.name}>
                <h3 className="text-3xl font-sans font-bold uppercase mb-2">{'#' + this.props.name}</h3>
                <div className="activities">
                    {this.state.lfgs.map((activity, index) => (
                        <div
                            className="activity p-4 my-4 mx-auto bg-white border-solid rounded border-2"
                            key={index}
                            data-index={index}
                        >
                            <h4 className="text-2xl font-sans font-bold uppercase mb-4">{activity.activity}</h4>
                            <p className="border-b-2 border-b-slate-300 text-sm mb-4 pb-4">
                                <span className="mr-2 font-bold">Start Time:</span>
                                <span className="mr-4">
                                    {moment
                                        .unix(activity.startTime)
                                        .tz(targetTimezone)
                                        .format('MMMM Do YYYY, h:mm A zz')}
                                </span>
                                <span className="font-bold mr-2">Guardians:</span>
                                <span className="mr-4">
                                    {activity.amountJoined} / {activity.amountNeeded}
                                </span>
                            </p>
                            <p className="p-4 bg-slate-600 text-white rounded-t">{activity.description}</p>
                            <ol className="flex flex-wrap">
                                {activity.whoJoined.map((guardian, guardianIndex) => (
                                    <li
                                        className={
                                            'flex-auto hover:cursor-pointer px-4 py-2 text-center break-all ' +
                                            (this.colorMap as any)[guardianIndex % 3].normal +
                                            ' ' +
                                            (this.colorMap as any)[guardianIndex % 3].hover
                                        }
                                        key={guardianIndex}
                                    >
                                        {guardian}
                                    </li>
                                ))}
                            </ol>
                        </div>
                    ))}
                </div>
            </div>
        );
    }

    public render() {
        const isSetup = this.state.setup;
        if (isSetup) {
            return this.renderSetup();
        } else {
            return this.renderFeed();
        }
    }
}

export default Feed;
