const feeds = document.getElementsByClassName('levelcrush-feed');
for (let i = 0; i < feeds.length; i++) {
    const feed = feeds[i];
    const interval =
        feed.getAttribute('data-interval') !== null ? parseInt(feed.getAttribute('data-interval') as string) : 60;
    const name = feed.getAttribute('data-name') !== null ? (feed.getAttribute('data-name') as string) : 'default';

    ReactDOM.render(
        <React.StrictMode>
            <Feed name={name} interval={interval}></Feed>
        </React.StrictMode>,
        feed,
    );
}
